    height: 4rem;
  }
  
  .sidebar_user_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }
  
  .sidebar_user span {
    margin-left: auto;
    width: 3.5rem;
    height: 3.5rem;
